@import url(https://fonts.googleapis.com/css2?family=Montserrat&display=swap);
body {
    margin: 0;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}
.container {
    padding: 64px;
}

.row{
    margin-left: 5%;
    margin-top: -6%;
}

.row:after {
    content: "";
    display: table;
    clear: both
}
.column {
    float: left;
    width: 33.3%;
    margin-bottom: 16px;
    padding: 0 8px;
    margin-top: 6%;
}
.card {
    margin: 8px;
}


.card-container {
    padding: 0 16px;
}

.card-container::after,
.row::after {
    content: "";
    clear: both;
    display: table;
}
  
.column-66 {
    float: left;
    width: 66.66666%;
    padding: 20px;
}
  
.column-33 {
    float: left;
    width: 33.33333%;
    padding: 20px;
}

  
.large-font {
    font-size: 40px;
    color: black;
    margin-top: 13%;
    background: linear-gradient(to right, red , yellow);
}

.description-x{
    text-align: justify;
    margin-top: 5%;
    margin-left: 0%;
    margin-right: 9%;
    font-weight: 300;
}

.subdescription{
    margin-top: 5%;
    text-align: justify;
    font-weight: 300;
    margin-left: 15%;
}

img {
    margin-left: -30%;
    /* width: 100%; */
   
}
img .logo-mundo{
    margin-left: -30%;
    width: 100%;
}



@media screen and (max-width: 900px) {

    .column-66,
    .column-33 {
      width: 100%;
      text-align: center;
    }
    .large-font {
        font-size: 42px;
        color: black;
        margin-top: 43%;
        background: white;
    }

    .description-x {
        text-align: justify;
        margin-top: 10%;
        margin-left: 1%;
    }

    img {
        margin: auto;
        width: 60%;
        margin-left: 0px;
    }
    .container {
        padding: 4px;
    }
    .card-container {
        padding: 0;
    }
    
}

@media screen and (max-width: 650px) {
    .column {
        width: 100%;
        display: block;
    }
}


/******************************* contact ***********************************/
.container-contact {
    border-radius: 5px;
    padding: 20px;
}

.form-contact {
    margin-left: 15%;
    margin-right: 15%;
}

input[type=text], select, textarea {
    width: 100%;
    padding: 12px;
    border: 1px solid #ccc;
    border-radius: 4px;
    box-sizing: border-box;
    margin-top: 6px;
    margin-bottom: 16px;
}

input[type=submit] {
    color: white;
    padding: 12px 20px;
    border: none;
    border-radius: 4px;
    cursor: pointer;
}

textarea{
    resize: none;
}

button {
    background-color: #4CAF50;
    color: white;
    padding: 14px 20px;
    margin: 8px 0;
    border: none;
    cursor: pointer;
    width: 100%;
}

button:hover {
    opacity: 0.8;
}

@media screen and (max-width: 650px) {
    .form-contact {
        margin-left: -2%; 
        margin-right: -6%; 
        margin-top: 30%; 
        
    }
}

/* ----------- iPad Pro ----------- */
@media only screen and (min-width: 1024px) and (max-height: 1366px) and (-webkit-min-device-pixel-ratio: 1.5) {
    .row {
        margin-left: 0%; 
    }
    .logo-mundo{
        width: 129%;
        margin-left: -30%;
    }
    
}

